import React, { useState } from "react";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import Stack from "@mui/material/Stack";
import TextField from "@mui/material/TextField";
import CircularProgress from "@mui/material/CircularProgress";
import axios from "axios";
import { motion } from 'framer-motion';

const ContactForm: React.FC = () => {
    const [email, setEmail] = useState('');
    const [message, setMessage] = useState('');
    const [emailSent, setEmailSent] = useState(false);
    const [isSending, setIsSending] = useState(false);

    const sleep = (waitTime: number) => new Promise( resolve => setTimeout(resolve, waitTime));

    const handleEmailchange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setEmail(event.target.value);
    };

    const handleMessagechange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setMessage(event.target.value);
    };

    const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();

        try {
            setIsSending(true);
            // 
            await axios.post('http://localhost:3001/send-mail', {
                email: email,
                message: message,
            })
            await sleep(5000);
            console.log("")
            // 
            setEmailSent(true);
        } catch (error) {
            console.log(error);
        } finally {
            setIsSending(false);
        };
        console.log('', email)
        console.log('', message)
    };

    return(
        <>
        <Box sx={{height: "50vh"}}>
            <Grid container rowSpacing={8} columnSpacing={2}>
                <Grid item xs={12} md={12} sx={{ textAlign: 'center' }}>
                <Typography 
                    variant="h2"
                    noWrap
                    component="a"
                    sx={{
                    mr: 2,
                    fontFamily: 'monospace',
                    fontWeight: 700,
                    letterSpacing: '.3rem',
                    color: '#000000',
                    textDecoration: 'none',
                    }}
                    >
                        <motion.div
                            variants={{
                                offscreen: { // 
                                y: 100,
                                opacity: 0,
                                },
                                onscreen: { // 
                                y: 0,
                                opacity: 1,
                                transition: {
                                    duration: 0.5,
                                },
                                },
                            }}
                            initial="offscreen" // offscreen
                            whileInView="onscreen" // onscreen
                            viewport={{ once: false, amount: 0 }}
                            >
                        Contact
                        </motion.div>
                    </Typography>
                </Grid>
                <Grid item xs={12} md={12}>
                    <motion.div
                            variants={{
                                offscreen: { // 
                                y: 100,
                                opacity: 0,
                                },
                                onscreen: { // 
                                y: 0,
                                opacity: 1,
                                transition: {
                                    duration: 0.5,
                                },
                                },
                            }}
                            initial="offscreen" // offscreen
                            whileInView="onscreen" // onscreen
                            viewport={{ once: false, amount: 0 }}
                            >
                    {emailSent ? (
                        <Box sx={{ width: "100%", textAlign: "center" }}>
                            
                        </Box>
                    ) : (
                        <Stack sx={{ width: "100%" }} component="form" spacing={2} onSubmit={handleSubmit} autoComplete="off">
                            <TextField required fullWidth label="" name="email" variant="outlined" value={email} onChange={handleEmailchange} />
                            <TextField required fullWidth multiline rows={4} label="" name="message" variant="outlined" value={message} onChange={handleMessagechange} />
                            {isSending ? (
                                <Button variant="contained" color="primary">
                                    <CircularProgress />
                                </Button>
                            ) : (
                                <Button type="submit" variant="contained" color="primary">
                                    
                                </Button>
                            )}
                        </Stack>
                    )}
                    </motion.div>
                </Grid>
            </Grid>
            </Box>
        </>
    );
};

export default ContactForm;